import { useCallback, useEffect, useRef, useState } from "react"
import { debounce } from "@rp/react/web/mui/core"

const useDebouncedState = <T>(value: T, timeout = 325): [T, (val: T) => void, T] => {
  const [rawValue, setRawValue] = useState(value)
  const [debouncedValue, setDebouncedValue] = useState(value)

  const ref = useRef<ReturnType<typeof debounce> | null>(null)
  useEffect(() => {
    ref.current = debounce(setDebouncedValue, timeout)
  }, [timeout])

  const setValue = useCallback((val: T) => {
    setRawValue(val)
    ref.current?.(val)
  }, [])

  return [debouncedValue, setValue, rawValue]
}

export { useDebouncedState }
